*,
*:before,
*:after {
  border: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font: 400 10px/1.5 "Open Sans", Arial, Helvetica, sans-serif;
}
h1 {
  font-size: 3.1em;
}
h2 {
  font-size: 2em;
  font-weight: 400;
}
h3 {
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 0.5px;
  line-height: 1;
}
h4 {
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 0.5px;
  line-height: 1;
}
p {
  font-size: 1.3em;
  line-height: 1.5;
}
img {
  border-width: 0;
}
hr {
  /* Also see tweaks.css */
  border: 0 solid transparent;
  border-top-width: 1px;
  height: 1px;
  margin: 5px 0;
  display: block;
  clear: both;
}
hr.dashed {
  border-top-style: dashed;
  margin: 10px 0;
}
hr.divider {
  display: none;
}
p.right {
  text-align: right;
}
p.jumpbox-return {
  line-height: 33px;
}
b,
strong {
  font-weight: bold;
}
i,
em {
  font-style: italic;
}
u {
  text-decoration: underline;
}
ul {
  list-style-type: disc;
}
ol {
  list-style-type: decimal;
}
li {
  display: list-item;
}
ul ul,
ol ul {
  list-style-type: circle;
}
ol ol ul,
ol ul ul,
ul ol ul,
ul ul ul {
  list-style-type: square;
}
*::-webkit-input-placeholder {
  opacity: 1;
}
*:-moz-placeholder {
  opacity: 1;
}
*::-moz-placeholder {
  opacity: 1;
}
*:-ms-input-placeholder {
  opacity: 1;
}
/* Main blocks
---------------------------------------- */
#wrap-body {
  padding: 20px 0 20px;
  position: relative;
}
#page-footer {
  clear: both;
}
#page-footer h3 {
  margin-top: 20px;
}
.side-block {
  margin-bottom: 20px;
}
.side-block > img {
  display: block;
  max-width: 100%;
}
.side-block-head {
  font-size: 1.2em;
  padding: 17px 20px;
  margin: 0;
}
.side-block-body {
  font-size: 1.3em;
  padding: 20px;
  margin: 0;
}
.side-block-body fieldset {
  font-size: 1em;
}
.side-block-body input {
  font-size: 1em;
}
.side-login #username,
.side-login #password,
.side-login #remember-me {
  display: block;
  float: left;
  margin: 0 0 10px;
  width: 100%;
}
.side-login #remember-me input {
  height: auto;
  margin: 0 10px 1px 0;
}
.logo {
  float: left;
  width: auto;
  margin: 41.5px 0;
}
.logo:hover {
  text-decoration: none;
}
/* Site description and logo */
#site-description {
  float: left;
}
#site-description h1 {
  margin-right: 0;
}
/* Simple body */
#simple-body h2 {
  line-height: 50px;
}
#simple-body {
  font-size: 1.2em;
}
#simple-body h2 {
  font-size: 1.6667em;
}
#simple-body .copyright {
  font-size: 0.9167em;
}
#simple-body p,
#simple-body fieldset,
#simple-body .action-bar,
#simple-body th,
#simple-body td {
  font-size: 1em;
}
/* Round cornered boxes and backgrounds
---------------------------------------- */
.navbar {
  padding: 3px 10px;
}
.forabg {
  margin-bottom: 20px;
  overflow: hidden;
}
.forumbg {
  margin-bottom: 20px;
  overflow: hidden;
}
.panel {
  margin-bottom: 20px;
  padding: 20px;
}
.post {
  padding: 0;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-position: 100% 0;
  position: relative;
}
.rowbg {
  margin: 5px 5px 2px 5px;
}
.content > *:last-child,
.inner > *:last-child,
.panel > *:last-child,
.column1 > *:last-child,
.column2 > *:last-child {
  margin-bottom: 0 !important;
}
.content > fieldset dl:last-child,
.column1 dl:last-child {
  padding-bottom: 0;
}
/* Horizontal lists
----------------------------------------*/
.navbar ul.linklist {
  padding: 2px 0;
}
ul.linklist {
  display: block;
  margin: 0;
}
ul.linklist > li {
  float: left;
  font-size: 1.1em;
  line-height: 2.2em;
  list-style-type: none;
  margin-right: 20px;
  width: auto;
}
ul.linklist > li.rightside,
p.rightside,
a.rightside {
  float: right;
  margin-right: 0;
  margin-left: 20px;
  text-align: right;
}
ul.navlinks {
  border: none;
}
ul.leftside {
  float: left;
  margin-left: 0;
  margin-right: 5px;
  text-align: left;
}
ul.rightside {
  float: right;
  margin-left: 5px;
  margin-right: -5px;
  text-align: right;
}
ul.linklist li.responsive-menu {
  position: relative;
  margin: 0 5px;
}
ul.linklist li.responsive-menu a.responsive-menu-link {
  display: inline-block;
  margin: 0 5px;
  font-size: 16px;
  position: relative;
  width: 16px;
  line-height: 20px;
  text-decoration: none;
}
.hasjs ul.linklist.leftside,
.hasjs ul.linklist.rightside {
  max-width: 48%;
}
.hasjs ul.linklist.fullwidth {
  max-width: none;
}
/* Bulletin icons for list items
----------------------------------------*/
ul.linklist.bulletin > li:before {
  display: inline-block;
  content: "\2022";
  font-size: inherit;
  line-height: inherit;
  padding-right: 4px;
}
ul.linklist.bulletin > li:first-child:before,
ul.linklist.bulletin > li.rightside:last-child:before {
  content: none;
}
ul.linklist.bulletin > li.no-bulletin:before {
  content: none;
}
.responsive-menu:before {
  display: none !important;
}
/* Dropdown menu
----------------------------------------*/
.dropdown-container {
  position: relative;
}
.dropdown-container-right {
  float: right;
}
.dropdown-container-left {
  float: left;
}
.nojs .dropdown-container:hover .dropdown {
  display: block !important;
}
.dropdown {
  position: absolute;
  left: 0;
  top: 1.2em;
  z-index: 2;
  padding: 0;
  margin-right: -500px;
}
.dropdown a {
  padding: 7.5px 20px;
  display: inline-block;
  line-height: 1.5;
  width: 100%;
  -webkit-transition: background-color 0.15s ease, border-color 0.15s ease, color 0.15s ease;
  transition: background-color 0.15s ease, border-color 0.15s ease, color 0.15s ease;
}
.dropdown.live-search {
  top: auto;
}
.dropdown-container.topic-tools {
  float: left;
}
.dropdown-up .dropdown {
  top: auto;
  bottom: 1.2em;
}
.dropdown-left .dropdown,
.nojs .rightside .dropdown {
  left: auto;
  right: 0;
  margin-left: -500px;
  margin-right: 0;
}
.dropdown-button-control .dropdown {
  top: 32px;
}
.dropdown-button-control.dropdown-up .dropdown {
  top: auto;
  bottom: 32px;
}
.dropdown .pointer {
  display: none;
}
.dropdown .dropdown-contents {
  z-index: 2;
  overflow: hidden;
  overflow-y: auto;
  padding: 10px 0;
  position: relative;
  min-width: 40px;
  max-height: 300px;
  border-radius: 3px;
  box-sizing: border-box;
}
.dropdown li {
  float: none !important;
  line-height: 27px !important;
  font-size: 1em !important;
  list-style: none;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  text-align: left;
}
.dropdown-contents > li > a {
  padding-right: 30px;
}
.dropdown-nonscroll > li > a {
  padding-right: 20px;
}
.dropdown li:first-child,
.dropdown li.separator + li,
.dropdown li li {
  border-top: 0;
}
.dropdown li li:first-child {
  margin-top: 4px;
}
.dropdown li li:last-child {
  padding-bottom: 0;
}
.dropdown li li {
  padding-left: 18px;
}
.wrap .dropdown li,
.dropdown.wrap li,
.dropdown-extended li {
  white-space: normal;
}
.dropdown li.separator {
  border-top: 1px solid transparent;
  margin: 5px 20px;
  padding: 0;
}
.dropdown li.separator:first-child,
.dropdown li.separator:last-child {
  display: none !important;
}
.dropdown-page-jump .dropdown-contents {
  padding-left: 20px;
  padding-right: 20px;
}
.dropdown-page-jump .page-jump-form > .inputbox {
  padding: 0 0 0 7px;
}
/* Table styles
----------------------------------------*/
table {
  border-collapse: collapse;
}
table.table1 {
