  width: auto;
  text-align: left;
  max-width: 100%;
}
.left-box.profile-details {
  width: 80%;
}
.right-box {
  float: right;
  width: auto;
  text-align: right;
  max-width: 100%;
}
dl.details dt {
  float: left;
  clear: left;
  width: 30%;
  text-align: right;
  display: block;
}
dl.details dd {
  margin-left: 0;
