/* Action-bars (container for post/reply buttons, pagination, etc.)
---------------------------------------- */
.action-bar {
  clear: both;
  display: inline-block;
  font-size: 12px;
  margin: 0;
  width: 100%;
}
.action-bar > div {
  margin-bottom: 20px;
}
.panel .action-bar:last-child > div {
  margin-bottom: 0;
}
.action-bar > .clear {
  margin: 0 !important;
}
.action-bar.compact {
  line-height: 33px;
  margin-bottom: 20px;
}
.forabg + .action-bar {
  margin-top: 2em;
}
/* Pagination
---------------------------------------- */
.pagination {
  float: right;
  text-align: right;
  width: auto;
  line-height: 33px;
}
.action-bar.bottom .pagination {
  margin-top: 0;
}
.pagination > ul {
  display: inline-block;
  list-style: none;
  margin-left: 20px;
}
.pagination > ul > li {
  display: inline-block !important;
  padding: 0;
  font-size: 100%;
  line-height: normal;
  vertical-align: middle;
}
.pagination li a,
.pagination li span {
  border: 1px solid transparent;
  display: block;
  font-size: 1em;
  font-weight: normal;
  line-height: 33px;
  height: 35px;
  width: 35px;
  text-align: center;
  text-decoration: none;
  -webkit-transition: background-color 0.15s ease, border-color 0.15s ease, color 0.15s ease;
  transition: background-color 0.15s ease, border-color 0.15s ease, color 0.15s ease;
}
.pagination li.ellipsis {
  display: none;
}
.pagination li.page-jump a {
  padding: 0 20px;
  width: auto;
}
.pagination li.page-jump a {
  width: auto;
}
.pagination > ul > li:first-child a,
.pagination > ul > li:first-child span {
